import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'
import router from '../router'
import {
  SnackbarProgrammatic as Snackbar
} from 'buefy'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLoading: false,
    loggedUser: null,
    lastTimeUpdated: new Date().getTime(),
    lastStartDatetimeUpdated: null,
    lastEndDatetimeUpdated: null,
    services: [],
  },
  mutations: {
    setServices(state, services) {
      state.services = services
    },
    setLastStartDatetimeUpdated(state, lastStartDatetimeUpdated) {
      state.lastStartDatetimeUpdated = lastStartDatetimeUpdated
    },
    setLastEndDatetimeUpdated(state, lastEndDatetimeUpdated) {
      state.lastEndDatetimeUpdated = lastEndDatetimeUpdated
    },
    setLoading(state, isLoading) {
      state.isLoading = isLoading;
    },
    setLoggedUser(state, loggedUser) {
      state.loggedUser = loggedUser;
    },
    logout(state) {
      state.loggedUser = null;
    }
  },
  actions: {
    logout({
      commit
    }) {
      commit('logout');
      commit('setLoading', false);
      // push to login page
      Snackbar.open({
        message: 'You have been logged out',
        type: 'is-warning',
        duration: 5000,
        queue: true
      });
      // check if we are in the login page
      router.replace('/login');
    }
  },
  getters: {
    getToken(state) {
      if (state.loggedUser && state.loggedUser.token) {
        return state.loggedUser.token;
      }
      return null
    },
  },
  modules: {},
  plugins: [new VuexPersistence({
    key: 'mgp-tournament-app',
    reducer: (state) => ({
      loggedUser: state.loggedUser
    }),
  }).plugin]
})
